.features {
  background: var(--#{$prefix}black);
  @include e(content) {
    display: grid;
  }
  @include e(col) {
    @include m(center) {
      position: relative;
      order: 1;
      @include before() {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        @include coloropacity($black, 0.7);
        z-index: 1;
      }
    }
  }
  @include e(info) {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: rem(100px) rem(20px);
    @include m(center) {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 1;
    }
  }
  @include e(img) {
    position: relative;
    height: 380px;
    width: 100%;
    padding-bottom: 100%;
    @include m(center) {
      position: relative;
      height: 100%;
    }
    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
}
