.story {
  padding: rem(70px) 0 rem(120px);
  @include e(content) {
    display: flex;
  }
  @include e(left) {
    flex: 0 0 40%;
    padding: 0 rem(15px);
    @include notlc() {
      margin-bottom: 0;
    }
  }
  @include e(right) {
    flex: 0 0 60%;
    padding: 0 rem(15px);
  }
  @include e(stamp) {
    right: rem(10px);
    top: rem(-50px);
  }
  @include e(img) {
    @include m(small) {
      position: absolute;
      bottom: rem(-79px);
    }
  }
}

