/* ------------------------------ LINK HOVER ------------------------------------ */

.link:hover::before {
  opacity: 1;
  transform: scale(1, 1);
}

.link:hover {
  color: var(--#{$prefix}sand);
  border-color: var(--#{$prefix}sand);
}

/* ------------------------------ LINK-2 HOVER ------------------------------------ */

.link-2:hover::before {
  opacity: 1;
  transform: scale(1, 1);
}

.link-2:hover {
  color: var(--#{$prefix}white);
  border-color: var(--#{$prefix}sand);
}

/* ------------------------------ BTN-1 HOVER ------------------------------------ */

.btn-1:hover .btn__text-one:first-child {
  transform: translateY(-150%);
}

.btn-1:hover .btn__text-two {
  top: 50%;
  transform: translateY(-50%);
}

.btn-1::before {
  transition-duration: 800ms;
  position: absolute;
  width: 200%;
  height: 200%;
  content: "";
  top: -200%;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  background: var(--#{$prefix}black);

  z-index: 1;
}

.btn-1:hover::before {
  top: -15%;
}

/* ------------------------------ BTN-2 HOVER ------------------------------------ */

.btn-2:hover .btn__text-one:first-child {
  transform: translateY(-150%);
}

.btn-2:hover .btn__text-two {
  top: 50%;
  transform: translateY(-50%);
}

.btn-2::before {
  transition-duration: 800ms;
  position: absolute;
  width: 200%;
  height: 200%;
  content: "";
  top: -200%;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  background: var(--#{$prefix}sand);
  z-index: 1;
}

.btn-2:hover::before {
  top: -15%;
}

/* ------------------------------ SLIDER ARROWS HOVER ------------------------------------ */

.prev::before,
.next::before {
  transition: all 0.3s ease 0s;
}

.prev:hover::before,
.next:hover::before {
  background: var(--#{$prefix}sand);
}

.prev::after,
.next::after {
  transition: all 0.3s ease 0s;
}

.prev:hover::after,
.next:hover::after {
  color: var(--#{$prefix}black);
}

/* ------------------------------ FLAVORS SECTION HOVER ------------------------------------ */

.flavors__img {
  overflow: hidden;

  @include after() {
    background: linear-gradient(
      to right,
      rgba(255, 255, 255, 0) 0%,
      rgba(255, 255, 255, 0.4) 100%
    );
    display: block;
    height: 100%;
    top: 0;
    left: -100%;
    transform: skewX(-25deg);
    width: 50%;
    z-index: 2;
  }
}

.flavors__img:hover::after {
  animation: shine 0.85s;
}

.flavors__img {
  transition: all 0.3s linear;

  @include hover() {
    transform: scale(1.02);
  }
}

.flavors__img:hover::before {
  transform: rotateY(180deg);
  transition-delay: 300ms;
}

/* ------------------------------ SPECIAL SECTION HOVER ------------------------------------ */

.special-slider__slide:hover .slide__img img {
  opacity: 0.6;
  transform: scale(1.05, 1.05) rotate(2deg);
}

.slide__img:hover::before {
  animation: circle 0.95s;
}

/* ------------------------------ TEAM SECTION HOVER ------------------------------------ */

.team__item:hover .team__overlay {
  transform: scale(1, 1);
  transform-origin: left center;
}

/* ------------------------------ WHY SECTION HOVER ------------------------------------ */

.card__item .card__icon {
  transition: all 500ms ease;
}

.card__item:hover .card__icon {
  transform: scale(-1) rotate(180deg);
}

// PAGE MENU

.pages-menu__link:hover:before {
  opacity: 1;
  visibility: visible;
}

.pages-menu__link:hover {
  color: var(--#{$prefix}sand);
  padding-left: rem(20px);
}

/* ------------------------------ RECENT SECTION HOVER ------------------------------------ */


.recent__img {
  display: block;
  img {
    transition: all 0.3s ease 0s;
  }
  overflow: hidden;
  @include after() {
    background: linear-gradient(
      to right,
      rgba(255, 255, 255, 0) 0%,
      rgba(255, 255, 255, 0.4) 100%
    );
    display: block;
    height: 100%;
    top: 0;
    left: -100%;
    transform: skewX(-25deg);
    width: 50%;
    z-index: 2;
  }
}

.recent__img:hover::after {
  animation: shine 0.85s;
}

.recent__img:hover {
  img {
    transform: scale(1.05, 1.05);
  }
}


/* ------------------------------ SERVICES SECTION HOVER ------------------------------------ */

.services {
  @include e(icon) {
    transition: all 0.3s linear;
    @include hover() {
      transform: rotateY(180deg);
    }
  }
}

/* ------------------------------ LOC SECTION HOVER ------------------------------------ */

.loc__item .loc__icon {
  transition: all 500ms ease;
}

.loc__item:hover .loc__icon {
  transform: scale(-1) rotate(180deg);
}
