.to-top {
  position: fixed;
  background: var(--#{$prefix}sand);
  width: rem(50px);
  height: rem(50px);
  border-radius: 50%;
  z-index: 50;
  right: rem(30px);
  bottom: rem(30px);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease 0s;
}

.fa-chevron-up {
  color: var(--#{$prefix}dark);
  font-weight: 500;
  font-size: rem(30px);
}

.to-top.active {
    opacity: 1;
    pointer-events: auto;
}