.story {
  position: relative;
  overflow: hidden;
  background: var(--#{$prefix}darkLighter);
  padding: rem(70px) 0 0;
  @include e(content) {
    text-align: center;
  }
  @include e(garlic) {
    display: none;
  }
  @include e(left) {
    z-index: 0;
    @include notlc() {
      margin-bottom: rem(120px);
    }
  }
  
  @include e(text) {
    @include notlc() {
      margin-bottom: rem(30px);
    }
  }
  @include e(phone) {
    display: block;
    @include notlc() {
      margin-bottom: rem(50px);
    }
  }
  @include e(btn) {
    display: inline-block;
    margin-bottom: rem(30px);
  }
  @include e(right) {
    position: relative;
  }
  @include e(stamp) {
    position: absolute !important;
    right: rem(10px);
    top: rem(-72px);
    @include before(134, 134) {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: url("../i/story/badge-1-bg.png") no-repeat;
    }
  }
  @include e(img) {
    @include m(big) {
      padding-left: rem(50px);
    }
    @include m(small) {
      position: absolute;
      bottom: rem(124px);
      width: rem(150px);
      padding: rem(50px) 0;
      z-index: 1;
      @include before(140) {
        background: url(../i/flavors/decor.svg) center repeat;
        top: 0;
        bottom: 0;
        left: 50%;
        margin-left: rem(-70px);
        display: block;
        z-index: -1;
      }
    }
  }
  @include e (img) {
    @include img ();
  }
}


