.best {
  background: var(--#{$prefix}darkLighter);

  @include e(content) {
    position: relative;
    z-index: 1;
    padding: rem(30px) 0;
  }

  @include e(info) {
    text-align: center;
  }

  @include e(item) {
    padding: rem(15px);
    text-align: center;

    @include notlc() {
      margin-bottom: rem(30px);
    }
  }
  @include e(img-box) {
    overflow: hidden;
    border-radius: rem(20px);
    margin-bottom: rem(15px);
  }

  @include e(img) {
    transform: scale(1.03);
    display: inline-block;
    overflow: hidden;
    @include imgH();
  }
}

// CHEF SECTION

.chef {
  background: var(--#{$prefix}darkLighter);

  @include e(wrapper) {
    background: var(--#{$prefix}black);
  }

  @include e(img) {
    img {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }
  @include e(typography) {
    padding: rem(70px) rem(10px);
    text-align: center;
  }
  @include e(bage) {
    margin-bottom: rem(20px);
  }
  @include e(title) {
    margin-bottom: rem(15px);
  }
  @include e(text) {
    color: var(--#{$prefix}white);
    line-height: 1.8;
  }
}

