.page__banner_chefs {
  position: relative;
  background: url("../i/chefs/chefs__bg.jpg") center center no-repeat;
  background-size: cover;
  padding-top: rem(300px);
  padding-bottom: rem(150px);
}

.main-chef {
  background: var(--#{$prefix}darkLighter);
  @include e(content) {
    padding: rem(30px) 0;
  }
  @include e(img) {
    @include img();
  }
  @include e(box) {
    padding: rem(80px) rem(20px) 0;
  }
  @include e(toptitle) {
    margin-bottom: rem(10px);
  }
  @include e(decor) {
    margin-left: 0;
  }
  @include e(text) {
    margin-bottom: rem(40px);
  }
  @include e(signature) {
    margin-bottom: rem(10px);
  }
}

// SECTION INTRO

.intro-2 {
  padding: rem(200px) 0;
  background: var(--#{$prefix}black);
  @include before() {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: url("../i/chefs/intro.jpg") no-repeat center center;
    background-size: cover;
    background-attachment: fixed;
    opacity: 0.4;
  }
  @include e(box) {
    position: relative;
    z-index: 1;
    text-align: center;
  }
}
