.dish {
    @include e (content) {
        display: flex;
        align-items: center;
    }

    @include e (left) {
        flex: 0 0 50%;
    }

    @include e (right) {
        flex: 0 0 50%;
        padding: 0 rem(20px) 0 rem(70px);
    }
    @include e (text) {
        max-width: rem(600px);
    }
}