.menu {
  @include e(img) {
    transition: all 0.5s linear;
    @include hover() {
      opacity: 0.7;
      transform: scale(1.05, 1.05);
    }
  }
  @include before(921, 1036) {
    background: url("../i/menu/bg-5.png") no-repeat;
    left: 0;
    bottom: 0;
    z-index: -1;
    animation: MoveUpDown 7s linear infinite;
  }
  @include after(641, 418) {
    background: url("../i/menu/bg-6.png") no-repeat;
    right: 0;
    top: 0;
     z-index: -1;
    animation: MoveUpTop 7s linear infinite;
  }
}