.who {
    @include e (wrapper) {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    @include e (col) {
        text-align: left;
        padding: 0 rem(15px);
        width: calc(100% / 12 * 6.4 - 30px);
        margin-bottom: rem(30px);
        @include nth-child (3) {
            flex: 1 0 auto;
        }
    }
    @include e (img) {
        margin-bottom: 0;
    }
}

.services {
   @include e (box) {
    grid-template-columns: repeat(2, 1fr);
    gap: rem(20px);
   }
    @include e (col) {
        flex: 0 0 50%;
        @include nth-child (2) {
            grid-column: -1/1;
            justify-self: center;
        }
    }
}

.why-2 {
    @include e (cards) {
        grid-template-columns: repeat(3, 1fr);
    }

}