.flavors {
  @include e (graphic) {
    display: grid;
    grid-template-columns: repeat(2, 285px);
    gap: 40px;
    justify-content: center;
  }

  @include e (item) {
    @include notlc () {
      margin-bottom: 0;
    }

    @include m (center) {
      grid-column: 1 / -1;
      justify-self: center;
    }
  }
}