.page__banner_about {
  position: relative;
  background: url("../i/about/bg.jpg") center center no-repeat;
  background-size: cover;
  padding-top: rem(300px);
  padding-bottom: rem(150px);
}

// SECTION WHO

.who {
  background: var(--#{$prefix}darkLighter);

  @include e(content) {
    padding: rem(30px) 0;
  }

  @include e(info) {
    text-align: center;
    color: var(--#{$prefix}white);
    margin-bottom: rem(50px);
  }

  @include e(col) {
    text-align: center;
    @include fch() {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  }

  @include e(short) {
    font-family: $secondFontFamily;
    font-size: calc(20px + (7 + 7 * 0.7) * ((100vw - 320px) / 1320));

    span {
      color: var(--#{$prefix}sand);
    }
  }

  @include e(img) {
    margin-bottom: rem(30px);
    @include img();
  }

  @include e(data-box) {
    @include m(decor) {
      background: url("../i/main-pattern.png");
    }
    z-index: 1;
    span {
      display: block;
    }
    padding: rem(80px) rem(20px);
    margin: 0 rem(15px);
    height: 100%;
    text-align: center;
    @include border(1px, $sand);

    @include before() {
      z-index: -1;
      @include border(1px, $sand);
      top: rem(12px);
      bottom: rem(12px);
      left: rem(-14px);
      right: rem(-14px);
    }
  }

  @include e(decor) {
    margin: rem(25px) auto;
  }

  @include e(description) {
    color: var(--#{$prefix}white);
    margin-bottom: rem(30px);
    line-height: 2;
  }

  @include e(signature) {
    margin-bottom: rem(15px);
  }

  @include e(link) {
    display: block;
    color: var(--#{$prefix}sand);

    @include notlc() {
      margin-bottom: rem(10px);
    }
  }

  @include e(subtitle) {
    margin-bottom: rem(5px);
  }

  @include e(text) {
    color: var(--#{$prefix}grey);
    margin-bottom: rem(25px);
  }
}

// SECTION EXPERIENCE

.experience {
  background: var(--#{$prefix}dark);

  @include e(img) {
    @include img();
  }

  @include e(box) {
    position: relative;
    z-index: 1;
    text-align: center;
    padding: rem(70px) rem(10px);
  }

  @include e(signature) {
    margin-top: rem(15px);
    margin-bottom: rem(15px);
  }

  @include e(btn) {
    margin-top: rem(25px);
  }

  @include e(text) {
    color: var(--#{$prefix}white);
    margin-top: rem(20px);
    line-height: 1.5;
  }
}

// SECTION SERVICES

.services {
  background: var(--#{$prefix}darkLighter);

  @include e(content) {
    padding: rem(30px) 0;
  }

  @include e(info) {
    text-align: center;
  }

  @include e(box) {
    display: grid;
  }

  @include e(col) {
    text-align: center;

    @include nth-child(2) {
      order: 1;
    }
  }

  @include e(item) {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: rem(50px);
  }

  @include e(icon) {
    background: var(--#{$prefix}black);
    padding: rem(5px);
    display: flex;
    border-radius: 50%;

    @include m(inversion) {
      order: -1;
    }

    margin-bottom: rem(20px);
  }

  @include e(name) {
    color: var(--#{$prefix}white);
    font-family: $secondFontFamily;
    font-size: calc(22px + (3 + 3 * 0.7) * ((100vw - 320px) / 1320));
  }

  @include e(description) {
    color: var(--#{$prefix}grey);
  }

  @include e(img) {
    padding: rem(20px) 0;
    @include img();
  }
}

// SECTION TESTEMONIALS

.testemonials-2 {
  background: url("../i/about/testemonials__bg.jpg") center center no-repeat;
  background-size: cover;
  background-attachment: fixed;

  @include e(content) {
    padding: rem(30px) 0;
  }

  @include e(info) {
    text-align: center;
  }

  @include e(slide) {
    background: var(--#{$prefix}black);
    padding: rem(40px) rem(25px);
  }

  @include e(rating) {
    margin-bottom: rem(20px);

    span {
      color: var(--#{$prefix}sand);
    }
  }

  @include e(text) {
    color: var(--#{$prefix}white);
    font-family: $secondFontFamily;
    font-size: rem(18px);
    line-height: 1.5;
    margin-bottom: rem(35px);
  }

  @include e(box) {
    display: flex;
    align-items: center;
    gap: rem(10px);
  }

  @include e(img) {
    overflow: hidden;
    border-radius: 50%;
    @include img();
  }

  @include e(name) {
    margin-bottom: rem(5px);
  }
}

.testemonials-2__bullets {
  display: none;
}

// SECTION WHY

.why-2 {
  background: var(--#{$prefix}darkLighter);

  @include e(content) {
    padding: rem(30px) 0;
  }

  @include e(info) {
    text-align: center;
  }

  @include e(img) {
    @include img();
    @include before() {
      width: 100%;
      height: rem(180px);
      left: 0;
      bottom: 0;
      background: -webkit-linear-gradient(
        bottom,
        rgba(0, 0, 0, 0.95),
        rgba(0, 0, 0, 0.8),
        rgba(0, 0, 0, 0.05)
      );
      z-index: 1;
    }
  }

  @include e(text) {
    font-size: calc(22px + (3 + 3 * 0.7) * ((100vw - 320px) / 1320));
    font-family: $secondFontFamily;
    color: var(--#{$prefix}white);
  }

  @include e(card) {
    position: relative;
    text-align: center;

    @include notlc() {
      margin-bottom: rem(25px);
    }
  }

  @include e(box) {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 1;
  }
}

// SECTION GALLERY

.gallery {
  background: var(--#{$prefix}darkLighter);

  @include e(content) {
    padding: rem(30px) 0;
  }

  @include e (wrapper) {
    padding: 0 0 rem(50px) 0;
  }

  @include e(slide) {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.gallery__prev,
.gallery__next {
  display: none;
}

.testemonials-2__bullets .swiper-pagination {
  bottom: -50px;
}
