.service {
  background: var(--#{$prefix}dark);

  @include e(btn) {
    text-align: center;
    width: 100%;
  }

  @include e(content) {
    overflow: hidden;
    background: var(--#{$prefix}black);
  }

  @include e(form) {
    padding: rem(30px) 0;
  }

  @include e(items) {
    cursor: pointer;
  }

  @include e(date) {
    cursor: pointer;
  }

  @include e(clock) {
    cursor: pointer;
  }

  @include e(area) {
    height: rem(130px);
    resize: none;
  }

  @include e(contact) {
    position: relative;
    text-align: center;
  }
}

.form {
  @include e(info) {
    text-align: center;
    margin-bottom: rem(40px);
  }

  @include e(title) {
    line-height: 1.17;
    margin-bottom: rem(15px);
  }

  @include e(request) {
    color: var(--#{$prefix}white);
    line-height: 1.81;
  }

  @include e(phone) {
    color: var(--#{$prefix}sand);
  }
}

.input__wrapper {
  color: var(--#{$prefix}white);
  position: relative;
  margin-bottom: rem(20px);
}

.service__left-icon {
  padding-left: rem(40px) !important;
}

.input__icon {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: rem(15px);
  line-height: 30px;
  margin-top: rem(-15px);
  pointer-events: none;
}

.input__arrow {
  position: absolute;
  right: rem(12px);
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}

.contact__inner {
  background: url("../i/main-pattern.png") left top no-repeat;
  background-size: cover;
  text-align: center;
  padding: rem(50px);
}

.contact__title {
  font-size: calc(28px + (22 + 22 * 0.7) * ((100vw - 320px) / 1320));
  margin-bottom: rem(40px);
}

.contact__text {
  color: var(--#{$prefix}grey);
}

.contact__phone {
  display: block;

  @include notlc() {
    margin-bottom: rem(20px);
  }
}

.contact__text {
  @include notlc() {
    margin-bottom: rem(25px);
  }
}
