@function font($file) {
  @return url($fontsPath + $file);
}

@function userfile($file) {
  @return url($usersFilesPath + $file);
}

@function rem($target) {
  @return ($target / $baseFontSize) * 1rem;
}

@mixin setCssVar($cssProp, $val) {
  $prefix: if(variable-exists(prefix), #{$prefix}, "");
  --#{$prefix}#{$cssProp}: #{$val};
}

@mixin getCssVar($cssProp, $cssVar: $cssProp) {
  $prefix: if(variable-exists(prefix), #{$prefix}, "");
  #{$cssProp}: var(--#{$prefix}#{$cssVar});
}

// --------------------------------- CONTAINER --------------------------------- //

@mixin container($width) {
  max-width: $width;
  margin: auto;
  padding: 0 rem(15px);
}

// --------------------------------- FONT --------------------------------- //

@mixin font-face($font-name, $file-name, $weight: normal, $style: normal) {
  @font-face {
    font-family: quote($font-name);
    font-style: $style;
    font-weight: $weight;
    src: url($file-name + ".eot");
    src: url($file-name + ".eot?#iefix") format("embedded-opentype"),
      url($file-name + ".woff") format("woff"),
      url($file-name + ".ttf") format("truetype"),
      url($file-name + ".svg##{$font-name}") format("svg");
  }
}

// --------------------------------- ELEMENTS --------------------------------- //

@mixin e($element) {
  &__#{$element} {
    @content;
  }
}

// --------------------------------- MODIFIER --------------------------------- //

@mixin m($modifier) {
  &_#{$modifier} {
    @content;
  }
}

// --------------------------------- FIRST CHILD NOT --------------------------------- //

@mixin notfc($children: true) {
  @if $children or $children == first-child {
    &:not(:first-child) {
      @content;
    }
  }
}

// --------------------------------- LAST CHILD NOT --------------------------------- //

@mixin notlc($children: true) {
  @if $children or $children == last-child {
    &:not(:last-child) {
      @content;
    }
  }
}

// --------------------------------- FIRST CHILD --------------------------------- //

@mixin fch() {
  &:first-child {
    @content;
  }
}

// --------------------------------- LAST CHILD --------------------------------- //

@mixin lch() {
  &:last-child {
    @content;
  }
}

// --------------------------------- NTH-CHILD --------------------------------- //

@mixin nth-child($points) {
  @each $point in $points {
    &:nth-child(#{$point}) {
      @content;
    }
  }
}

// --------------------------------- RGBA OPACITY --------------------------------- //

@mixin coloropacity($color, $opacity) {
  background: $color;
  background: rgba($color, $opacity);
}

// --------------------------------- BORDER OPACITY --------------------------------- //

@mixin borderopacity($sz, $color, $opacity) {
  border: $sz solid rgba($color, $opacity);
}

// --------------------------------- BORDER BOTTOM OPACITY --------------------------------- //

@mixin borderopacity-b($sz, $color, $opacity) {
  border-bottom: $sz solid rgba($color, $opacity);
}

// --------------------------------- BORDER TOP OPACITY --------------------------------- //

@mixin borderopacity-t($sz, $color, $opacity) {
  border-top: $sz solid rgba($color, $opacity);
}

// --------------------------------- BORDER RIGHT OPACITY --------------------------------- //

@mixin borderopacity-r($sz, $color, $opacity) {
  border-right: $sz solid rgba($color, $opacity);
}

// --------------------------------- BORDER --------------------------------- //

@mixin border($sz, $color) {
  border: $sz solid $color;
  @content;
}

// --------------------------------- BORDER TOP --------------------------------- //

@mixin border-t($sz, $color) {
  border-top: $sz solid $color;
}

// --------------------------------- BORDER BOTTOM --------------------------------- //

@mixin border-b($sz, $color) {
  border-bottom: $sz solid $color;
}

// --------------------------------- BORDER RIGHT --------------------------------- //

@mixin border-r($sz, $color) {
  border-right: $sz solid $color;
}

// --------------------------------- BORDER LEFT --------------------------------- //

@mixin border-l($sz, $color) {
  border-left: $sz solid $color;
}
// --------------------------------- HOVER, FOCUS--------------------------------- //

@mixin hover {
  &:hover {
    @content;
  }
}

@mixin focus {
  &:focus {
    @content;
  }
}

// --------------------------------- BEFORE, AFTER--------------------------------- //

@mixin before($w: none, $h: none) {
  & {
    position: relative;
  }

  &::before {
    content: "";
    position: absolute;
    width: #{$w}px;
    height: #{$h}px;
    @content;
  }
}

@mixin after($w: 0, $h: 0) {
  & {
    position: relative;
  }

  &::after {
    content: "";
    position: absolute;
    width: #{$w}px;
    height: #{$h}px;
    @content;
  }
}

// --------------------------------- ADAPTIVE FONT--------------------------------- //

@mixin adaptiv-font($mobSize, $pcSize, $maxWidth) {
  $addSize: $pcSize - $mobSize;
  $addMobSize: $addSize + $addSize * 0.7;
  font-size: calc(
    #{$mobSize + px} + #{$addMobSize} * ((100vw - 320px) / #{$maxWidth})
  );
  // font-size: calc(#{$mobSize + px} + #{$addSize} * (100vw / #{$maxWidth}));
  @content;
}

// --------------------------------- FLEX END--------------------------------- //

@mixin flex-end() {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

// --------------------------------- FLEX CENTER--------------------------------- //

@mixin flex-center() {
  display: flex;
  align-items: center;
  justify-content: center;
}

// --------------------------------- FLEX BETWEEN--------------------------------- //

@mixin flex-bt() {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

// --------------------------------- RESPONSIVE IMG --------------------------------- //

@mixin img() {
  img {
    max-width: 100%;
    width: 100%;
    display: block;
    height: auto;
  }
}

// --------------------------------- HACK IMG --------------------------------- //

@mixin imgH() {
  & {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
  }
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}

// --------------------------------- BACKGROUND OPACITY --------------------------------- //

@mixin bg-opacity($color, $opacity) {
  background: $color;
  background: rgba($color, $opacity);
}


