.why {
  background: var(--#{$prefix}dark);
  padding: rem(70px) 0;
  @include e(content) {
    text-align: center;
    position: relative;
    z-index: 2;
  }

  @include e (card-box) {
    display: grid;
    gap: rem(30px);
  }
}



.card__item {
  background: var(--#{$prefix}black);
  padding: rem(30px) rem(20px);
  @include nth-child (odd) {
    background: var(--#{$prefix}lightDark);
  }
}
.card__icon {
  @include notlc() {
    margin-bottom: rem(20px);
  }
}
.card__title {
  font-size: calc(22px + (3 + 3 * 0.7) * ((100vw - 320px) / 1320));
  line-height: 1.22;
  text-transform: capitalize;
  color: var(--#{$prefix}white);
  font-family: $secondFontFamily;
  @include notlc() {
    margin-bottom: rem(20px);
  }
}
.card__text {
  color: var(--#{$prefix}grey);
  font-size: rem(14px);
  line-height: 1.57; 
}

