.dropdown__img {
  @include border(1px, transparent);
  overflow: hidden;
  display: block;
  transition: all 0.3s ease 0s;
  @include hover() {
    @include border(1px, $sand);
  }
  img {
    display: block;
    transition: all 0.3s ease 0s;
    @include hover() {
      transform: scale(1.1);
    }
  }
}


.book__btn {
  @include before() {
    animation-name: rotateme;
    animation-duration: 15s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
}