.why {
  @include before() {
    width: rem(120px);
    height: rem(115px);
    background: url("../i/service/02.png") no-repeat;
    left: 0;
    bottom: 0;
  }
  @include after() {
    width: rem(208px);
    height: rem(178px);
    background: url("../i/service/01.png") no-repeat;
    right: 0;
    top: rem(-70px);
  }
  @include e(card-box) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}



