.features {
  @include e(content) {
    grid-template-columns: repeat(2, 1fr);
  }
  @include e(col) {
    @include nth-child(3) {
      grid-column: span 2/3;
    }
    @include m(center) {
      order: 0;
      @include before() {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        @include coloropacity($black, 0.3);
        z-index: 1;
      }
    }
  }
}
