
.special-slider{
  margin: 0 rem(30px);
}
.slide__img {
  position: relative;
  overflow: hidden;
  background: var(--#{$prefix}sand);
  img {
    transition: all 0.3s ease 0s;
    display: block;
    width: 100%;
  }
   @include before() {
    top: 50%;
    left: 50%;
    z-index: 1;
    display: block;
    width: 0;
    height: 0;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 0%;
    transform: translate(-50%, -50%);
    opacity: 0;
  }
}



