.header {
  @include e(top-left) {
    display: block;
    @include after() {
      width: rem(8px);
      height: rem(8px);
      top: 24%;
      left: 60%;
      @include border(1px, $sand);
      transform: rotate(45deg);
      z-index: 1;
    }
  }

  @include e(top-right) {
    display: block;
    margin: 0;
  }
}

.burger__btn {
  display: none;
}

.lower__menu-content {
  @include flex-end();
  gap: rem(30px);
}

.lower__menu {
  display: flex;
}

.lower__item {
  @include notlc() {
    margin-right: rem(38px);
  }
}

// DROPDOWN MENU

.dropdown-menu__box {
  transition: all 0.3s linear;
  transform: translateY(-20%);
}

.dropdown-menu {
  background: var(--#{$prefix}black);
  position: relative;
  left: 0;
  right: 0;
  z-index: 2;
}

.dropdown-menu__content {
  max-width: 1300px;
  margin: auto;
}

.dropdown-menu__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-wrap: wrap;
}

.active-menu {
  visibility: visible;
  opacity: 1;
  height: 100%;
  transform: translateY(0);
}

.dropdown__item {
  text-align: center;
  padding: 15px;
  flex: 0 0 25%;
}

.dropdown__img {
  display: block;
  overflow: hidden;
  @include imgH();
}

.dropdown__title {
  display: block;
  padding: rem(15px) 0;
  text-transform: uppercase;
  font-size: rem(13px);
  line-height: 2.23;
  font-weight: 700;
  letter-spacing: rem(1.95px);
  color: var(--#{$prefix}white);
}

// PAGES MENU

.pages-menu {
  position: absolute;
  top: 0;
  right: 9px;
  width: rem(250px);
  padding: rem(25px) 0;
  background: var(--#{$prefix}black);
  border-radius: rem(10px);
  transition: all 0.3s linear;
}

.pages-menu__item {
  text-transform: uppercase;
  padding-left: rem(25px);
  @include notlc() {
    padding-bottom: rem(15px);
  }
}

.pages-menu__link {
  transition: all 0.3s linear;
  @include before(8, 8) {
    @include border(1px, $sand);
    top: rem(7px);
    left: 0;
    transform: rotate(45deg);
    opacity: 0;
    visibility: hidden;
  }
}

.active-pages {
  opacity: 1;
  visibility: visible;
  top: rem(40px);
}
