.menu {
    @include e (box) {
          display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: rem(60px);

    @include before () {
        left: 50%;
        top: 10%;
        height: 80%;
        @include borderopacity-r (1px, $grey, 0.7);
    } 
    }
    @include e (item) {
       @include notlc () {
        margin-bottom: 0;
    } 
    }
}

