.appetirez {
    @include e (box) {
        flex-direction: row;
       justify-content: center;
    }

    @include e (item) {
        @include notlc () {
            margin-bottom: rem(35px);
        }
    }

    @include e (img) {
        padding: 0 rem(15px);
        margin-bottom: 0;
        flex: 0 0 60%;
    }
    @include e (menu) {
        padding: 0 rem(15px);
        flex: 0 0 40%;
    }
}


