.service {
  @include e(content) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    top: rem(-270px);
    margin-bottom: rem(-270px);
    z-index: 1;
  }
  @include e(form) {
    flex: 0 0 66.666667%;
  }
  @include e(contact) {
    flex: 0 0 33.33333%;
  }
}

.input__wrapper_person {
  flex: 0 0 32%;
}

.input__wrapper_date {
  flex: 0 0 32%;
}

.input__wrapper_time {
  flex: 0 0 32%;
}

