.team {
  @include before(207, 478) {
    background: url("../i/team/decor-left.png") no-repeat;
    left: 0;
    bottom: 0;
    display: none;
  }
   @include after(252, 187) {
    background: url("../i/team/decor-right.png") no-repeat;
    right: 0;
    top: 0;
    display: none;
  }
  background: var(--#{$prefix}dark);
  @include e(content) {
    text-align: center;
    padding: rem(70px) 0;
  }
  @include e(info) {
    text-align: center;
  }

  @include e(img) {
    position: relative;
  }
}

.team__item .team__overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.3s ease 0s;
  transform: scale(0, 1);
  transform-origin: right center;
  background-position: center center;
  transition: all 900ms ease;
}

.team__item .team__overlay:before {
  position: absolute;
  content: "";
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  opacity: 0.4;
  background-color: var(--#{$prefix}sand);
}

.overlay__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  transition: all 900ms ease;
}
.social__box {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.team__img img {
  width: 100%;
  display: block;
  height: auto;
}

.social__link {
  color: var(--#{$prefix}white);
  display: flex;
  align-items: center;
  justify-content: center;
  width: rem(40px);
  height: rem(40px);
  background: var(--#{$prefix}black);
  padding: rem(15px);
  @include notlc() {
    margin-right: rem(15px);
  }
}
