.flavors {
  padding: 0 0 rem(20px);
  @include e(graphic) {
   
    grid-template-columns: repeat(3, 1fr);
    gap: 150px;
    padding-bottom: rem(50px);
  }
  @include e(info) {
    position: relative;
    top: rem(160px);
  }
  @include e (item) {
    @include nth-child (2) {
       margin-top: rem(160px);
    }
  }
 @include before (246, 412) {
  background: url("../i/flavors/bg-1.png") no-repeat;
  left: 0;
  bottom: 0;
  animation: MoveUpDown 7s linear infinite;
 } 
  @include after (343, 335) {
  background: url("../i/flavors/bg-2.png") no-repeat;
  right: 0;
  top: 0;
  animation: MoveUpTop 7s linear infinite;
 } 
}

