.recent {
  background: var(--#{$prefix}black);

  @include e(content) {
    text-align: center;
    padding: rem(70px) 0;
  }

  @include e(item) {
    @include notlc() {
      margin-bottom: rem(30px);
    }
  }

  @include e(date) {
    padding: rem(2px);
    font-size: rem(12px);
    line-height: 1.16;
    font-weight: 700;
    position: absolute;
    top: rem(30px);
    left: rem(25px);
    color: var(--#{$prefix}sand);
    background: var(--#{$prefix}black);
    z-index: 1;
  }

  @include e(img) {
    @include img();
  }

  @include e(box) {
    position: relative;
    z-index: 3;
    padding: 0 rem(25px) rem(25px) rem(25px);
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
  }

  @include e(inner) {
    position: absolute;
    bottom: rem(45px);
    left: 0;
  }

  @include e(desc) {
    font-family: $secondFontFamily;
    color: var(--#{$prefix}white);
    font-size: rem(22px);
    font-weight: 500;
  }

  @include e(btn) {
    margin-top: rem(30px);
  }
}

.image__box {
  @include before() {
    left: 0;
    bottom: 0;
    width: 100%;
    height: 170px;
    // background: rgba(0, 0, 0, 0.7);
    background: -webkit-linear-gradient(
      bottom,
      rgba(0, 0, 0, 0.9),
      rgba(0, 0, 0, 0.7),
      rgba(0, 0, 0, 0)
    );
    z-index: 1;
  }
}
