/* ------------------------------ ANIMATION BOOK TABLE ------------------------------------ */

@keyframes rotateme {
  0% {
    transform: rotate(0deg);
    opacity: 1;
  }

  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
    opacity: 1;
  }
}

/* ------------------------------ HEADER SLIDER SECTION ------------------------------------ */

.header-slider__slide img {
  animation: scale 15s linear infinite;
}

@keyframes scale {
  50% {
    transform: scale(1.2);
  }
}

/* ------------------------------ FLAVORS SECTION ------------------------------------ */

@keyframes MoveUpTop {
  0%,
  100% {
    top: 0;
  }

  50% {
    top: 100px;
  }
}

@keyframes MoveUpDown {
  0%,
  100% {
    bottom: 0;
  }

  50% {
    bottom: 100px;
  }
}

/* ------------------------------ STORY SECTION ------------------------------------ */

.story__stamp {
  @include before(134, 134) {
    animation-name: rotateme;
    animation-duration: 15s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
}

@keyframes rotateme {
  0% {
    transform: rotate(0deg);
    opacity: 1;
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
    opacity: 1;
  }
}

/* ------------------------------ DISH SECTION ------------------------------------ */


@keyframes float_up_down {
  0% {
    transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }

  50% {
    transform: translateY(30px);
    transform: translateY(30px);
    opacity: 1;
  }

  100% {
    transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }
}

/* ------------------------------ MENU SECTION ------------------------------------ */

.menu__left-decor {
  animation: MoveUpTop 7s linear infinite;
}

@keyframes MoveUpTop {
  0%,
  100% {
    top: 0;
  }

  50% {
    top: 100px;
  }
}

.menu__right-decor {
  animation: MoveUpBottom 7s linear infinite;
}

@keyframes MoveUpBottom {
  0%,
  100% {
    bottom: 0;
  }

  50% {
    bottom: 100px;
  }
}

/* ------------------------------ INTRO SECTION ------------------------------------ */

.intro__play {
  @include before() {
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    @include border(15px, $white);
    border-radius: 50%;
    opacity: 0.5;
    animation: ripple 3s linear infinite;
  }
  @include after() {
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    @include border(15px, $white);
    border-radius: 50%;
    opacity: 0.3;
    animation: ripple 3s 1s linear infinite;
  }
}

@keyframes ripple {
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

/* ------------------------------ SHINE ANIMATION FLAVORS SECTION ------------------------------------ */

@keyframes shine {
  100% {
    left: 125%;
  }
}

/* ------------------------------ CIRCLE ANIMATION SPECIAL SECTION ------------------------------------ */

@keyframes circle {
  0% {
    opacity: 1;
  }
  40% {
    opacity: 1;
  }
  100% {
    width: 200%;
    height: 200%;
    opacity: 0;
  }
}


