.testimonials {
  @include e(contnet) {
    padding: rem(70px) 0 rem(70px);
  }
  background: url("../i/testimonials/bg.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  padding: rem(120px) 0 rem(340px);
  @include before() {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: var(--#{$prefix}dark);
    opacity: 0.8;
  }
}

.testimonials-slider__first-slide {
  color: var(--#{$prefix}white);
  text-align: center;
  font-family: $secondFontFamily;
}

.testimonials-slider__first-quote {
  font-size: rem(70px);
}
.testimonials-slider__first-text {
  font-size: calc(20px + (15 + 15 * 0.7) * ((100vw - 320px) / 1320));
}

.testimonials-slider__second {
  max-width: rem(500px);
}

.testimonials-slider__user {
  cursor: pointer;
  overflow: hidden;
  border-radius: 50%;
  display: inline-block;
  margin-bottom: rem(15px);
}

.testimonials-slider__user img {
  max-width: 100%;
  width: 100%;
  display: block;
}
.testimonials-slider__name {
  font-size: rem(12px);
  color: var(--#{$prefix}sand);
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: rem(2.4px);
}

.testimonials-slider__second-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: rem(50px) rem(15px);
  transition: all 0.3s ease 0s;
}

.swiper-slide-thumb-active {
  transform: scale(1.3);
}
