.intro {
  background: var(--#{$prefix}black);
  @include before() {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: url("../i/intro/bg.jpg") no-repeat center center;
    background-size: cover;
    background-attachment: fixed;
    opacity: 0.4;
  }
  @include e(content) {
    padding: rem(70px) 0;
  }
  @include e(info) {
    position: relative;
    text-align: center;
    z-index: 1;
  }
  @include e(title) {
    text-transform: none;
  }
  @include e(play) {
    color: var(--#{$prefix}white);
    cursor: pointer;
    display: inline-block;
    width: rem(100px);
    height: rem(100px);
    border-radius: 50%;
    @include border(1px, $white);
  }
  @include e(icon) {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  @include e(line) {
    @include before() {
      width: 100%;
      height: rem(18px);
      top: rem(30px);
      background: url("../i/intro/decor.svg") left top;
      background-repeat: repeat-x;
    }
  }
}

.counter {
  padding-top: rem(100px);
  text-align: center;
  @include e(inner) {
    position: relative;
    z-index: 3;
  }
  @include e(item) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include notlc() {
      margin-bottom: rem(50px);
    }
    span {
      display: block;
    }
  }
  @include e(num) {
    font-family: $secondFontFamily;
    font-size: calc(34px + (26 + 26 * 0.7) * ((100vw - 320px) / 1320));
    color: var(--#{$prefix}white);
  }
  @include e(sign) {
    font-family: $secondFontFamily;
    font-size: calc(25px + (20 + 20 * 0.7) * ((100vw - 320px) / 1320));
    color: var(--#{$prefix}white);
  }
  @include e(text) {
    text-transform: uppercase;
    font-size: rem(12px);
    line-height: 1.5;
    letter-spacing: rem(4.8px);
    font-weight: 700;
    color: var(--#{$prefix}sand);
    max-width: rem(64px);
  }
}

