// SECTION TABLE

.table {
  @include e (content) {
    padding: rem(70px) 0;
  }
  @include e(form) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  @include e (text) {
    margin: auto;
    max-width: 800px;
  }
  @include e (btn) {
    width: auto;
     flex: 0 0 auto;
  }
}

.input__wrapper_person-2 {
  flex: 0 0 25%;
}

.input__wrapper_date-2 {
  flex: 0 0 25%;
}

.input__wrapper_time-2 {
  flex: 0 0 25%;
}



// SECTION LOCATION

.loc {
  @include e (content) {
    padding: rem(70px) 0;
  }
  @include e(items) {
    grid-template-columns: repeat(3, 1fr);
  }
}
