.dish {
  background: var(--#{$prefix}dark);

  @include e(content) {
    text-align: center;
  }

  @include e(right) {
    padding: rem(70px) 0 rem(70px);
  }

  @include e(bage) {
    @include notlc() {
      margin-bottom: rem(20px);
    }
  }

  @include e(price) {
    padding: rem(40px) 0;

    @include m(old) {
      font-size: rem(18px);
      color: var(--#{$prefix}grey);
      padding-right: rem(20px);
    }

    @include m(new) {
      font-size: rem(24px);
      color: var(--#{$prefix}sand);
    }
  }
  @include e(img) {
    @include imgH();
  }
}
