.fixed-header {
  background: var(--#{$prefix}darkGrey);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  transition: all 0.3s ease 0s;

  @include e(content) {
    max-width: rem(1700px);
    margin: 0 auto;
    padding: 0 rem(15px);
  }

  @include e(top) {
    @include flex-bt();
    font-size: rem(14px);
  }

  @include e(top-left) {
    color: var(--#{$prefix}white);
    display: none;
  }
}

.location {
  position: relative;
  padding-left: rem(20px);

  @include before() {
    content: "\f3c5";
    font-family: "fontAwesome";
    left: 0;
  }

  @include notlc() {
    margin-right: rem(70px);
  }
}

.time {
  padding-left: rem(20px);

  @include before() {
    content: "\f017";
    font-family: "fontAwesome";
    left: 0;
  }
}

.header__top-right {
  display: none;
}

.phone {
  color: var(--#{$prefix}white);
  padding-left: rem(20px);

  @include before() {
    content: "\f095";
    font-family: "fontAwesome";
    left: 0;
  }

  @include notlc() {
    margin-right: rem(70px);
  }
}

.mail {
  color: var(--#{$prefix}white);
  padding-left: rem(20px);

  @include before() {
    content: "\f0e0";
    font-family: "fontAwesome";
    left: 0;
  }
}

.header__lower {
  padding: rem(30px) 0;
}

.lower {
  @include flex-bt();
  @include e(logo) {
    z-index: 2;
  }
  @include e(menu) {
    position: relative;
    display: none;
    z-index: 2;
  }
  @include e(btn) {
    position: relative;
    display: none;
    z-index: 2;
  }
}



.dropdown-menu__box {
  visibility: hidden;
  opacity: 0;
  height: 0;
}

.pages-menu {
  visibility: hidden;
  opacity: 0;
}

/* ------------------------------ MAIN SLIDER ------------------------------------ */

.header-slider {
  position: relative;
}

.header-slider__slide img {
  width: 100%;
  max-width: 100%;
  height: 100vh;
  display: block;
  object-fit: cover;
}

.header__prev,
.header__next {
  display: none;
}

.header-slider__content {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  z-index: 3;
  transform: translate(0, -50%);
}

.header-slider__item {
  text-align: center;
}

.header-slider__decor img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: inline-block;
}

.header-slider__title {
  font-size: clamp(1.875rem, -0.17rem + 10.23vw, 7.5rem);
  // @include adaptiv-font(30, 60, 1200);
}

.header-slider__text {
  @include adaptiv-font(16, 20, 1200);
}

.header-slider__btn {
  margin-top: rem(40px);
  display: inline-block;
  background: transparent;
  color: var(--#{$prefix}sand);
  @include border(1px, $sand);
}

.book__btn {
  transform: scale(0.7);
  right: 15px;
  bottom: 15px;
  display: block;
  position: absolute !important;
  width: rem(110px);
  height: rem(110px);
  z-index: 10;
  @include before() {
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    @include border(1px, $sand);
    transform: rotate(45deg);
  }
}

.book__box {
  text-align: center;
  display: block;
  width: rem(110px);
  height: rem(110px);
  background: var(--#{$prefix}sand);
  z-index: 1;
  padding: 12px 10px 0px;
}

.book__text {
  text-transform: uppercase;
  font-size: rem(12px);
  font-weight: 700;
  line-height: 1.5; /* 18/12 */
  letter-spacing: 1.8px;
  color: var(--#{$prefix}black);
}

.header__next {
  margin-right: rem(50px);
}

.header__prev {
  margin-left: rem(50px);
}
