.services {
    @include e (item) {
        gap: rem(20px);
        text-align: left;
        flex-direction: row;
    }
    @include e (data) {
        @include m (inversion) {
            text-align: right;
        }
    }
    @include e (icon) {
        @include m (inversion) {
            order: 1;
        }
    }
}

.why-2 {
    @include e (cards) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: rem(25px);
}

}