.dish {
    @include e (right) {
        padding: rem(70px) rem(20px) rem(70px) rem(70px);
        position: relative;
    }

    @include e (bage) {
        position: absolute;
        left: rem(28px);
    }
}

.dish__bage {
  animation-name: float_up_down;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}