


.why-2 {
  @include e (box) {
    bottom: rem(20px);
  }
}

.why-2__box:first-child(1) {
  bottom: -30px;
}

.services {
  @include before(277, 280) {
    top: rem(-90px);
    left: 0;
    background: url("../i/about/bg-14.png") no-repeat;
  }
}

.gallery__prev,
.gallery__next {
  display: flex;
}

.gallery__prev {
  left: rem(40px);
}

.gallery__next {
  right: rem(40px);
}

// SECTION TESTEMONIALS

.testemonials-2 {
  @include e(bullets) {
    display: block;
  }
  @include e(slider) {
    padding: rem(50px) 0;
  }
}

.gallery__slide {
  img {
    transition: all 0.3s ease 0s;
    @include hover() {
      filter: grayscale(80%);
      transform: scale(1.02);
      cursor: pointer;
    }
  }
}
