.fixed-header {
  background: var(--#{$prefix}darkGrey);
  transform: translateY(-60px);
}

.header {
  @include e(top) {
    position: relative;
    padding: rem(20px) 0;
    z-index: 2;
    @include borderopacity-b (1px, $grey, 0.5);
  }
  @include e(top-right) {
    position: relative;
    display: block;
    margin: auto;
    z-index: 2;
    @include after() {
      width: rem(8px);
      height: rem(8px);
      top: 24%;
      right: 57%;
      @include border(1px, $sand);
      transform: rotate(45deg);
      z-index: 1;
    }
  }
}



.phone {
  @include notlc() {
    margin-right: rem(50px);
  }
}

.lower__btn {
  display: block;
}
