.who {
  @include e(content) {
    padding: rem(70px) 0;
  }

  @include e(col) {
    width: calc(100% / 12 * 4.3 - 30px);
    margin-bottom: 0;
  }
}

.experience {
  @include e(wrapper) {
    display: flex;
    align-items: center;
  }

  @include e(left) {
    flex: 0 0 50%;
  }

  @include e(right) {
    padding: 0 rem(15px);
    flex: 0 0 50%;
  }

  @include e(img) {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  @include e(text) {
    margin: 0 auto;
    max-width: rem(420px);
  }
}

.inversion-left {
  order: 1;
}

.services {
  @include e(content) {
    padding: rem(70px) 0;
  }
  @include e(box) {
    align-items: center;
    grid-template-columns: repeat(3, 1fr);
  }

  @include e(col) {
    flex: 0 0 50%;

    @include nth-child(2) {
      order: 0;
      grid-column: auto;
      justify-self: auto;
    }
  }
}

.testemonials-2 {
  @include e(content) {
    padding: rem(70px) 0;
  }
}

.why-2 {
  @include e(content) {
    padding: rem(70px) 0;
  }
  @include e(cards) {
    grid-template-columns: repeat(4, 1fr);
  }
  @include e(card) {
    @include notlc() {
      margin-bottom: 0;
    }
  }
}

.gallery {
  @include e(content) {
    padding: rem(70px) 0;
  }
}
