.footer {
  background: var(--#{$prefix}black);
  @include before() {
    width: 100%;
    height: 100%;
    background: url("../i/footer/bg.jpg") no-repeat left top;
    background-size: cover;
    opacity: 0.4;
  }

  @include e(content) {
    position: relative;
    padding: rem(70px) 0;
    text-align: center;
    z-index: 1;
  }

  @include e(col) {
    padding: rem(50px) rem(30px);

    @include m(info) {
      @include before() {
        width: rem(15px);
        height: 100%;
        background: url("../i/pattern-line.svg");
        top: 0;
        left: 0;
      }

      @include after() {
        width: rem(15px);
        height: 100%;
        background: url("../i/pattern-line.svg");
        top: 0;
        right: 0;
      }
      background: var(--#{$prefix}dark) url("../i/footer/pattern.png") center
        top repeat;
    }
  }

  @include e(logo) {
    display: block;

    @include notlc() {
      margin-bottom: rem(40px);
    }
  }

  @include e(info) {
    color: var(--#{$prefix}grey);
  }

  @include e(news) {
    @include notlc() {
      margin-bottom: rem(30px);
    }
  }

  @include e(box) {
    @include notlc() {
      margin-bottom: rem(20px);
    }
  }

  @include e(link) {
    font-size: rem(12px);
    line-height: 1.5;
    text-transform: uppercase;
    color: var(--#{$prefix}grey);
    font-weight: 700;
    letter-spacing: rem(3px);
  }

  @include e(copyright) {
    text-align: center;
    position: relative;
    z-index: 1;
    padding-bottom: rem(50px);
    color: var(--#{$prefix}grey);

    a {
      color: var(--#{$prefix}sand);
    }
  }
  @include e(form) {
    position: relative;
  }

  @include e(input) {
    margin-bottom: rem(15px);
  }

  @include e(btn) {
    cursor: pointer;
    width: 100%;
  }
}

.info {
  @include e (text) {
    @include adaptiv-font (13, 16, 1200);
  }
  @include e(item) {
    line-height: 2.5;
  }

  @include e(link) {
    color: var(--#{$prefix}sand);
  }
}

.news {
  @include e(title) {
    @include adaptiv-font (20, 35, 1200);
    font-family: $secondFontFamily;
    color: var(--#{$prefix}white);
  }

  @include e(select) {
    color: var(--#{$prefix}white);
  }
}
