
/* ------------------------------ ACTIVE LINK ------------------------------------ */

.active__link {
  color: var(--#{$prefix}sand);
  padding-bottom: rem(5px);
  @include before() {
    left: 0;
    right: 0;
    bottom: -5px;
    width: 100%;
    height: rem(8px);
    opacity: 0;
    transform: scale(0.2, 1);
    transition: all 500ms ease;
    @include border-t(2px, $sand);
    @include border-b(2px, $sand);
  }
}


/* ------------------------------ SMALL LINK ------------------------------------ */

.small-link {
  font-size: rem(12px);
  text-transform: uppercase;
  color: var(--#{$prefix}white);
}



/* ------------------------------ DROPDOWN MENU ICON ------------------------------------ */

.lower__link_icon {
  @include after() {
    content: "\f0d7";
    font-family: "FontAwesome";
    top: 0;
    right: -5px;
  }
}

.lower__link_icon.icon-arrow__open::after {
  transform: rotateX(180deg);
  top: 15px;
}

/* ------------------------------ DROPDOWN MOBILE MENU ICON ------------------------------------ */

.mobile__link_icon {
  color: var(--#{$prefix}white);
  @include after() {
    content: "\f0d7";
    font-family: "FontAwesome";
    top: 28%;
    right: 5%;
    font-size: rem(18px);
  }
}

.mobile__link_icon.mobile-arrow__open::after {
  color: var(--#{$prefix}sand);
  transform-origin: center;
  transform: scaleY(-1) translateY(-20px);
}


/* ------------------------------ LINKS ------------------------------------ */

.link {
  text-transform: uppercase;
  font-size: rem(12px);
  font-weight: 700;
  letter-spacing: rem(2.4px);
  color: var(--#{$prefix}white);
  padding-bottom: rem(5px);

  @include before() {
    left: 0;
    right: 0;
    bottom: -5px;
    width: 100%;
    height: rem(8px);
    opacity: 0;
    transform: scale(0.2, 1);
    transition: all 500ms ease;
    @include border-t(2px, $sand);
    @include border-b(2px, $sand);
  }
}

.link-2 {
  text-transform: uppercase;
  font-size: rem(12px);
  font-weight: 700;
  letter-spacing: rem(2.4px);
  color: var(--#{$prefix}sand);
  padding-bottom: rem(5px);

  @include before() {
    left: 0;
    right: 0;
    bottom: -5px;
    width: 100%;
    height: rem(8px);
    opacity: 0;
    transform: scale(0.2, 1);
    transition: all 500ms ease;
    @include border-t(2px, $sand);
    @include border-b(2px, $sand);
  }
}

.link-3 {
  color: var(--#{$prefix}white);
  font-size: rem(15px);
  line-height: 1.5;
  letter-spacing: rem(2.4px);
}

/* ------------------------------ BUTTON ------------------------------------ */

.btn-1 {
  position: relative;
  overflow: hidden;
  display: inline-block;
  padding: rem(20px) rem(45px);
  @include border(1px, $sand);
  background: var(--#{$prefix}sand);
  color: var(--#{$prefix}black);
}

.btn-2 {
  position: relative;
  overflow: hidden;
  display: inline-block;
  background: transparent;
  padding: rem(20px) rem(45px);
  @include border(1px, $sand);
}

.btn__text-one {
  color: var(--#{$prefix}sand);
  font-size: rem(12px);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: rem(3px);
  transition: all 0.3s ease;
}

.black {
  color: var(--#{$prefix}black);
}

.btn__text-two {
  position: absolute;
  top: 100%;
  color: var(--#{$prefix}dark);
  font-size: rem(12px);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: rem(3px);
  transition: all 0.3s ease;
  z-index: 2;
}

.sand {
  color: var(--#{$prefix}sand);
}

.sand-big {
  color: var(--#{$prefix}sand);
  left: 0;
  right: 0;
}

/* ------------------------------ TITLE, SUBTITLE, TEXT ------------------------------------ */

.toptitle {
  display: block;
  text-transform: uppercase;
  font-size: rem(12px);
  font-weight: 700;
  color: var(--#{$prefix}sand);
  letter-spacing: rem(4.8px);
  @include notlc() {
    margin-bottom: rem(10px);
  }
}

.decor {
  width: rem(100px);
  margin: auto;

  @include notlc() {
    margin-bottom: rem(30px);
  }
}

.decor-2 {
  margin: auto;
  margin-bottom: rem(15px);
  @include border(1px, $sand) {
    width: rem(8px);
    height: rem(8px);
    transform: rotate(45deg);
  }
}

.title {
  // @include adaptiv-font (28, 50, 1200);
  font-size: clamp(1.75rem, 1.023rem + 3.64vw, 3.75rem);
  font-family: $secondFontFamily;
  text-transform: capitalize;
  line-height: 1;
  color: var(--#{$prefix}white);
  margin-bottom: rem(40px);
}

.subtitle {
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: rem(40px);
  color: var(--#{$prefix}white);
}

.small-title {
  font-family: $secondFontFamily;
  @include adaptiv-font (20, 35, 1200);
  text-transform: capitalize;
  font-weight: 500;
  line-height: 1.81;
  color: var(--#{$prefix}white);
  margin-bottom: rem(5px);
}

.text {
  line-height: 1.6;
  color: var(--#{$prefix}white);
}

.small-text {
  font-size: rem(14px);
  color: var(--#{$prefix}grey);
  line-height: 1.5;
  margin-bottom: rem(20px);
}

.phone-big {
  color: var(--#{$prefix}sand);
  font-size: rem(24px);
}

.name {
  color: var(--#{$prefix}white);
  font-size: rem(24px);
  line-height: 1.2;
  font-family: $secondFontFamily;
  text-transform: capitalize;
  font-weight: 500;
  margin-bottom: rem(10px);
}

.price {
  color: var(--#{$prefix}sand);
  font-size: rem(20px);
  font-family: $secondFontFamily;
  line-height: 1.45;
}

/* ------------------------------ SLIDER ARROWS ------------------------------------ */

.prev::before,
.next::before {
  content: "";
  left: 0;
  top: 0;
  transform: rotate(45deg);
  @include border(1px, #e4c590);
  width: rem(45px);
  height: rem(45px);
  flex: 0 0 auto;
}

.next::after {
  font-size: rem(25px);
  color: var(--#{$prefix}sand);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: absolute;
  top: 0;
  left: rem(5px);
  right: 0;
}

.prev::after {
  font-size: rem(25px);
  color: var(--#{$prefix}sand);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: absolute;
  top: 0;
  left: rem(-5px);
  right: 0;
}

.prev.swiper-button-prev.left {
  z-index: 50;
  position: absolute;
  left: -50px;
}

/* ------------------------------ SLIDER BULLETS ------------------------------------ */

.bullets .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  background: var(--#{$prefix}sand);
}

/* ------------------------------ INPUTS ------------------------------------ */

input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="url"],
input[type="file"],
input[type="number"],
input[type="date"],
textarea,
select {
  position: relative;
  display: inline-flex;
  height: rem(56px);
  font-size: rem(16px);
  width: 100%;
  color: var(--#{$prefix}white);
  line-height: 34px;
  font-weight: 400;
  padding: 10px 20px;
  background-color: var(--#{$prefix}darkLighter);
  @include borderopacity(1px, $grey, 0.7);
  transition: all 300ms ease;

  @include focus() {
    outline: none;
  }
}

input::-webkit-calendar-picker-indicator {
  opacity: 0;
}

select {
  appearance: none;
}

/* ------------------------------ SEPARATOR ------------------------------------ */

.separator {
  margin: rem(30px) 0;
  display: flex;
  justify-content: center;
  gap: rem(5px);

  span {
    position: relative;
    display: inline-block;
    width: rem(10px);
    height: rem(10px);
    @include border(1px, $sand);
  }
}


