.special {
  background: var(--#{$prefix}darkLighter);
  padding: rem(70px) 0 rem(70px);
  @include e(content) {
    text-align: center;
  }
  @include e(inner) {
    padding: rem(70px) rem(15px);
    background: var(--#{$prefix}black);
     @include before () {
    width: rem(15px);
    height: 100%;
    top: 0;
    left: 0;
    background: url("../i/pattern-line.svg");
  }
    @include after () {
    width: rem(15px);
    height: 100%;
    top: 0;
    right: 0;
    background: url("../i/pattern-line.svg");
  }
  }
  @include e(title) {
    font-size: calc(28px + (22 + 22 * 0.7) * ((100vw - 320px) / 1320));
  }
}

.special-slider__slide {
  padding-bottom: 70px;
}

.slide__img {
  margin-bottom: rem(15px);
  @include img();
}

// .slide__img img {
