.flavors {
  position: relative;
  overflow: hidden;
  background: var(--#{$prefix}dark);
  padding: rem(70px) 0 rem(70px);

  @include e(info) {
    position: relative;
    text-align: center;
  }

  @include e(item) {
    text-align: center;
    z-index: 1;

    @include notlc() {
      margin-bottom: rem(50px);
    }
  }
  @include e(text) {
    line-height: 1.81;

    p {
      margin-bottom: 40px;
    }

    max-width: rem(420px);
    margin: auto;
  }
  @include e(img) {
    img {
      @include img ();
    }
    z-index: 0;
    padding: rem(30px) 0;
    display: block;

    @include before(140) {
      background: url(../i/flavors/decor.svg) center repeat;
      top: 0;
      bottom: 0;
      left: 50%;
      margin-left: rem(-70px);
      display: block;
      z-index: -1;
      transition: all 0.9s linear;
    }
    @include notlc() {
      margin-bottom: rem(25px);
    }
  }
  @include e(title) {
    color: var(--#{$prefix}white);
  }
  @include e(subtitle) {
    color: var(--#{$prefix}white);
    font-size: calc(20px + (15 + 15 * 0.7) * ((100vw - 320px) / 1320));
    font-family: $secondFontFamily;
    margin-bottom: rem(20px);
  }
}

