.page__banner_contact {
  position: relative;
  background: url("../i/contact/contact_bg.jpg") center center no-repeat;
  background-size: cover;
  padding-top: rem(300px);
  padding-bottom: rem(150px);
}

.map-size {
  height: rem(500px);
}

// SECTION CONTACTS

.contacts {
  margin-bottom: rem(-70px);
  background: var(--#{$prefix}darkLighter);
  @include e(content) {
    padding: rem(30px) 0;
  }
  @include e(box) {
    position: relative;
    top: rem(-70px);
  }
  @include e(top) {
    text-align: center;
    background: var(--#{$prefix}dark);
  }
  @include e(item) {
    padding: rem(30px) rem(20px);
  }
  @include e(name) {
    color: var(--#{$prefix}white);
    font-family: $secondFontFamily;
    font-weight: 500;
    line-height: 1.08;
    font-size: calc(22px + (3 + 3 * 0.7) * ((100vw - 320px) / 1320));
    margin-bottom: rem(20px);
  }
  @include e(text) {
    display: block;
    color: var(--#{$prefix}grey);
  }
  @include e(link) {
    font-size: rem(16px);
    text-transform: capitalize;
    display: block;
    margin-top: rem(10px);
  }

  @include e(textarea) {
    height: rem(130px);
    resize: none;
    margin-bottom: rem(30px);
  }
  @include e(bottom) {
    padding: rem(20px);
    text-align: center;
    background: var(--#{$prefix}black);
  }
  @include e(title) {
    margin-bottom: rem(10px);
  }
  @include e(description) {
    color: var(--#{$prefix}grey);
    line-height: 1.8;
    margin-bottom: rem(30px);
  }
  @include e(input) {
    margin-bottom: rem(15px);
  }
  @include e(btn) {
    margin-bottom: rem(15px);
  }
  @include e(img) {
    position: relative;
    width: 100%;
    padding-top: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
