/* ------------------------------ MOBILE BAR ------------------------------------ */

.mobile__bar {
  position: fixed !important;
  overflow: auto;
  background: var(--#{$prefix}black);
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: -100%;
  transition: left 0.3s linear;
  z-index: 3;

  &.active {
    transition: all 0.3s ease 0s;
    @include before() {
      position: fixed;
      width: 100%;
      height: rem(70px);
      top: 0;
      left: 0;
      background: var(--#{$prefix}black);
      @include border-b (1px, $grey);
      z-index: 3;
    }
    left: 0;
  }
}

.mobile__content {
  padding: rem(75px) 0 rem(50px);
}
.mobile__logo {
  position: fixed;
  top: rem(9px);
  z-index: 3;
  @include notlc() {
    margin-bottom: rem(30px);
  }
}
.mobile__menu {
  padding: 0 rem(30px);
  @include notlc() {
    margin-bottom: rem(50px);
  }
}
.mobile__item {
  padding: rem(15px) 0;
  @include borderopacity-b(1px, #a7a7a7, 0.5);
  @include m (decor) {
    padding-left: rem(20px);
    @include before (8, 8) {
      @include border (1px, $sand);
      transform: rotate(45deg);
      top: rem(20px);
      left: 0;
    }
  }
}

.mobile__info {
  text-align: center;
  color: var(--#{$prefix}white);
  max-width: rem(270px);
  margin: auto;
}
.mobile__title {
  text-transform: capitalize;
  font-size: rem(28px);
  line-height: 1.17;
  font-weight: 500;
  margin-bottom: rem(15px);
}
.mobile__text {
  line-height: 1.5;
  color: var(--#{$prefix}grey);
}

.mobile__mail {
  color: var(--#{$prefix}sand);
}
.mobile__decor {
  display: inline-block;
  width: rem(8px);
  height: rem(8px);
  @include border(1px, #e4c590);
  transform: rotate(45deg);
  margin: rem(30px) 0;
}

/* ------------------------------ MOBILE DROPDOWN MENU ------------------------------------ */

.dropdown-mobile {
  display: none;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease 0s;
}

.dropdown-mobile.mobile-open {
  display: block;
  visibility: visible;
  opacity: 1;
}

.active-mobile__link a {
  color: var(--#{$prefix}sand);
}

.mobile__link {
  font-size: rem(18px);
}

/* ------------------------------ BURGER BTN ------------------------------------ */

.burger__btn {
  position: relative;
  display: inline-block;
  width: rem(30px);
  height: rem(18px);
  z-index: 5;

  span {
    position: absolute;
    top: calc(50% - 1px);
    left: 0;
    width: 100%;
    height: rem(2px);
    background: var(--#{$prefix}white);
    transition: all 0.2s;

    @include fch() {
      top: 0;
    }

    @include lch() {
      top: auto;
      bottom: 0;
    }
  }

  &.active {
    top: rem(-21px);
    transition: all 0.3s ease 0s;
    padding: rem(15px);
    border-radius: 50%;
    background: var(--#{$prefix}sand);
    span {
      background: var(--#{$prefix}black);
      transform: scale(0);

      @include fch() {
        transform: rotate(-45deg);
        top: calc(50% - 1px);
      }

      @include lch() {
        transform: rotate(45deg);
        bottom: calc(50% - 1px);
      }
    }
  }
}

