.menu {
  @include e(categories) {
    flex-direction: row;
    justify-content: center;
    gap: rem(45px);
  }
  @include e(categories-item) {
    @include notlc() {
      margin-bottom: 0;
    }
     @include notlc() {
        @include after (8, 8 ) {
            @include border (1px, $sand);
            transform: rotate(45deg);
            top: rem(8px);
            right: rem(-25px);
        }
    }
  }
  @include e(box-name) {
    display: flex;
    justify-content: space-between;
    z-index: 1;
    @include notlc() {
      margin-bottom: rem(10px);
    }
    @include before() {
      width: 100%;
      height: rem(6px);
      top: 25%;
      z-index: -1;
      @include borderopacity-t(1px, $grey, 0.7);
      @include borderopacity-b(1px, $grey, 0.7);
    }
  }

  @include e(name) {
    padding-right: rem(15px);
  }
  @include e(price) {
    padding-left: rem(15px);
  }
}

.menu__inner {
  flex: 1 1 auto;
}

