.banner {
  position: relative;
  background: url("../i/menu/menu_01_bg.jpg") center center no-repeat;
  background-size: cover;
  padding-top: rem(300px);
  padding-bottom: rem(150px);

  @include before() {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    @include coloropacity($black, 0.7);
  }

  @include e(content) {
    text-align: center;
  }

  @include e(info) {
    position: relative;
    z-index: 1;
  }
}

// APPERTIREZ SECTION

.appetirez {
  background: var(--#{$prefix}darkLighter);

  @include e(content) {
    padding: rem(30px) 0;
    text-align: center;
  }

  @include e(box) {
    display: flex;
    flex-direction: column;
  }

  @include e(img) {
    @include img();
  }

  @include e(menu) {
    padding: rem(30px) 0;
  }

  @include e(item) {
    display: block;

    @include notlc() {
      margin-bottom: rem(25px);
    }
  }

  @include e(title) {
    font-weight: 500;
    font-size: calc(28px + (22 + 22 * 0.7) * ((100vw - 320px) / 1320));
  }

  @include e(box-name) {
    @include flex-bt();
    margin-bottom: rem(10px);
  }

  @include e(name) {
    @include notlc() {
      margin-bottom: 0;
    }
  }
}

// MAIN SECTION

.main {
  background: var(--#{$prefix}darkLighter);

  @include e(content) {
    padding: rem(30px) 0;
    text-align: center;
  }

  @include e(img) {
    order: -1;
  }
}

// DESSERT SECTION

.dessert {
  background: var(--#{$prefix}darkLighter);

  @include e(content) {
    padding: rem(30px) 0;
    text-align: center;
  }
}

// DRINKS SECTION

.drinks {
  background: var(--#{$prefix}darkLighter);

  @include e(content) {
    padding: rem(30px) 0;
    text-align: center;
  }

  @include e(img) {
    order: -1;
  }
}

.reservation {
  background: url("../i/menu/reservation_01.jpg") center center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  @include e(content) {
    padding: rem(30px) 0;
  }
  @include e(wrapper) {
    background: var(--#{$prefix}black);
  }
  @include e(form) {
    padding: rem(25px) rem(15px);
  }

  @include e(contact) {
    text-align: center;
    background: url("../i/menu/reservation_02.jpg") left top no-repeat;
    background-size: cover;
  }
  @include e(box) {
    max-width: rem(240px);
    margin: auto;
  }
}

.reservation__inner {
  padding: rem(50px) rem(20px);
}

.reservation__title {
  font-size: rem(21px);
}

.discount {
  padding-top: rem(50px);
  padding-bottom: rem(70px);
  color: var(--#{$prefix}sand);
  font-size: rem(12px);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: rem(2.4px);
  position: relative;
  z-index: 2;
  @include after(170, 170) {
    top: rem(15px);
    left: rem(36px);
    background: var(--#{$prefix}darkLighter);
    z-index: -1;
    border-radius: rem(20px);
    @include border(3px, $black);
  }
  @include e(toptitle) {
    margin-bottom: 0;
  }
  @include before(170, 170) {
    top: rem(15px);
    left: rem(36px);
    background: var(--#{$prefix}black);
    border-radius: rem(20px);
    z-index: -1;
  }
}

.discount__num {
  font-family: $secondFontFamily;
  color: var(--#{$prefix}white);
  font-size: rem(60px);
  font-weight: 400;
}

.reservation__info {
  margin-bottom: rem(55px);
}
.reservation__text {
  color: var(--#{$prefix}grey);
  line-height: 1.56;
}
