.menu {
  background: var(--#{$prefix}darkLighter);
  position: relative;
  overflow: hidden;
  z-index: 1;

  @include e(left-decor) {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    img {
      filter: invert(7%);
      width: 100%;
      max-width: 100%;
      height: auto;
      display: block;
    }
  }

  @include e(right-decor) {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;

    img {
      filter: invert(7%);
      width: 100%;
      max-width: 100%;
      height: auto;
      display: block;
    }
  }

  @include e(content) {
    padding: rem(70px) 0;
    text-align: center;
  }

  @include e(title) {
    font-size: calc(28px + (22 + 22 * 0.7) * ((100vw - 320px) / 1320));
  }

  @include e(categories) {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: rem(25px) 0;
    @include borderopacity-t(1px, $grey, 0.7);
    @include borderopacity-b(1px, $grey, 0.7);
  }

  @include e(categories-item) {
    cursor: pointer;
    line-height: 1.56;
    text-transform: uppercase;
    font-family: $secondFontFamily;
    color: var(--#{$prefix}white);

    @include notlc() {
      margin-bottom: rem(20px);
    }
  }

  @include e(box) {
    padding: rem(50px) 0;
  }

  @include e(item) {
    display: flex;
    align-items: center;
    gap: 25px;

    @include notlc() {
      margin-bottom: rem(55px);
    }
  }

  @include e(img) {
    flex: 0 0 auto;
    width: 90px;
    height: 90px;
    overflow: hidden;
    border-radius: rem(20px);

    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }

  @include e(inner) {
    text-align: left;
    font-family: $secondFontFamily;
  }

  @include e(name) {
    font-size: rem(21px);
    line-height: 1.19;
    color: var(--#{$prefix}white);
    font-weight: 500;
    background: var(--#{$prefix}darkLighter);

    @include notlc() {
      margin-bottom: rem(10px);
    }
  }

  @include e(price) {
    background: var(--#{$prefix}darkLighter);

    @include notlc() {
      margin-bottom: rem(10px);
    }
  }

  @include e(description) {
    font-size: rem(14px);
    line-height: 1.57;
    font-family: $baseFontFamily;
    color: var(--#{$prefix}grey);
  }

  @include e(open) {
    color: var(--#{$prefix}white);
    line-height: 1.81;
    span {
      color: var(--#{$prefix}sand);
    }

    @include notlc() {
      margin-bottom: rem(25px);
    }
  }
}