.best {
  @include e(list) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
  @include e(img) {
    transition: all 0.3s ease 0s;
     @include hover () {
        @include before () {
           @include bg-opacity ($sand, 0.3);
        }
    }
   @include before () {
    transition: all 0.3s ease 0s;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
   }
   @include hover() {
        transform: scale(1.07);
      }
  }
}
