.page__banner_reservation {
  position: relative;
  background: url("../i/reservation/reservation__bg.jpg") center center
    no-repeat;
  background-size: cover;
  padding-top: rem(300px);
  padding-bottom: rem(150px);
}

// SECTION TABLE
.table {
  background: var(--#{$prefix}darkLighter);
  @include e(content) {
    position: relative;
    z-index: 1;
    padding: rem(30px) 0;
  }
  @include e(info) {
    position: relative;
    z-index: 1;
    text-align: center;
    margin-bottom: rem(40px);
  }
  @include e(title) {
    margin-bottom: rem(10px);
  }
  @include e(text) {
    color: var(--#{$prefix}grey);
    line-height: 1.8;
    margin-bottom: rem(15px);
  }
  @include e(booking) {
    color: var(--#{$prefix}white);
    span a {
      color: var(--#{$prefix}sand);
    }
  }
  @include e(form) {
    text-align: center;
    margin-bottom: rem(50px);
  }
  @include e (opentable) {
    margin: auto;
  }
  @include e(btn) {
    width: 100%;
    margin-bottom: rem(20px);
  }
  @include e(opentable) {
    text-align: center;
    color: var(--#{$prefix}white);
  }
}

// SECTION MAP

iframe {
  width: 100%;
  height: 600px;
}

.map {
  margin-bottom: rem(50px);
  @include e (left) {
    margin-bottom: rem(30px);
  }
   @include e (right) {
    text-align: center;
  }
}

// SECTION LOCATION

.loc {
  background: var(--#{$prefix}darkLighter);
  @include e(content) {
    padding: rem(30px) 0;
  }
  @include e(item) {
    padding: rem(30px) rem(20px);
    text-align: center;
    display: flex;
    flex-direction: column;
  }
 
  @include e(icon) {
    margin-bottom: rem(20px);
  }
  @include e(name) {
    color: var(--#{$prefix}white);
    font-size: rem(22px);
    font-family: $secondFontFamily;
    line-height: 1.22;
    margin-bottom: rem(20px);
  }
  @include e (toptitle) {
    height: 100%;
  }
  @include e(link) {
    color: var(--#{$prefix}sand);
  }
}
