// SECTION TABLE

.table {
  @include e(form) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.input__wrapper_person-2 {
  flex: 0 0 49%;
}

.input__wrapper_date-2 {
  flex: 0 0 49%;
}

.input__wrapper_time-2 {
  flex: 1 0 auto;
}

// SECTION MAP

iframe {
  height: 100%;
}

.map {
   display: flex;
  @include e(left) {
    flex: 0 0 60%;
    padding: 0 rem(15px);
    margin-bottom: 0;
  }
    @include e(right) {
      flex: 0 0 40%;
    padding: 0 rem(15px);
  }
 
}

// SECTION LOCATION

.loc {
  @include e(items) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
