//fonts
$google-font: "https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Forum&display=swap";
$prefix: vitalii- !default;
$baseFontSize: 16px;
$baseFontFamily: 'DM Sans', sans-serif;
$secondFontFamily: 'Forum', serif;
$regularFontWeight: 400;
$mediumFontWeight: 500;


$mobilePadding: (12px / $baseFontSize) * 1rem;
$desktopPadding: 1rem;

// Paths

$imagesPath: "../i/";
$fontsPath: "../fonts/";
$usersFilesPath: "../userfiles/";

// Colors
$black: #000;
$dark: #0e0d0c;
$darkLighter: #171819;
$darkGrey: #202020;
$lightDark: #151616;
$white: #ffffff;
$grey: #a7a7a7 !default;
$sand: #e4c590 !default;



$colors: (
  "black": $black,
  "dark": $dark,
  "darkLighter": $darkLighter,
  "darkGrey": $darkGrey,
  "lightDark": $lightDark,
  "white": $white,
  "grey": $grey,
  "sand": $sand,
) !default;


