
.starter {
    background: var(--#{$prefix}darkLighter);
    @include e (info) {
        text-align: center;
    }
    @include e (content) {
        padding: rem(30px) 0;
    }
}


.fresh {
    background: var(--#{$prefix}darkLighter);
