// EVENTS SECTION


.events {
    background: var(--#{$prefix}darkLighter);
    @include e (content) {
        padding: rem(30px) 0;
    }
    @include e (form) {
        display: flex;
        flex-direction: column;
    }
    @include e (info) {
        padding: rem(50px) rem(25px);
        background: url('../i/main-pattern.png');
        background-color: $black;
        text-align: center;
    }
    @include e (phone) {
        display: block;
        margin-bottom: rem(25px);
    }
    @include e (img) {
        @include notlc () {
            margin-bottom: rem(15px);
        }
        img {
           width: 100%;
           height: 100%;
           display: block;
           object-fit: cover;
        }
    }
}

.mt {
    margin-top: rem(40px);
}

.mb {
    order: 1;
    margin-bottom: rem(40px);
}


.decor-left{
    @include before () {
        width: rem(15px);
        height: 100%;
        background: url('../i/pattern-line.svg');
        top: 0;
        left: 0;
    }
}

.decor-right {
    @include after () {
        width: rem(15px);
        height: 100%;
        background: url('../i/pattern-line.svg');
        top: 0;
        right: 0;
    }
}
