.best {
    @include e (list) {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
    @include e (content) {
        padding: rem(70px) 0;
      }
}


.chef {
    @include e (wrapper) {
        display: flex;
        align-items: center;
    }
    @include e (img) {
        flex: 0 0 50%;
    }
    @include e (typography) {
        padding: rem(70px) rem(10px) rem(70px) rem(100px);
    }
    @include e (bage) {
        left: rem(60px);
    }
}